import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Board } from './board';
import { Teamboard } from './teamboard';
import { List } from './list';
import { Card } from './card';
import { Observable } from 'rxjs/Observable';
import { Teams } from '../homepage/team';
import  { Auth } from '../auth/auth';
import { environment } from '../../environments/environment';

@Injectable()
export class ServiceService {

  constructor(private http:HttpClient) { }
  base_url = environment.baseUrl;
  postboard = this.base_url+"/boards";
  getboardByUserId = this.base_url+"/Userboard";
  getboardbyid = this.base_url+"/board";
  getboardbyteam = this.base_url+"/Teamboard";
  getstarboard =this.base_url+"/GetStaredBoard"
  addstarboard = this.base_url+"/AddStaredBoard";
  removestarboard = this.base_url+"/RemoveStaredBoard";
  getteamstarboard = this.base_url+"/GetTeamStaredBoard";
  createlist = this.base_url+"/board-list";
  listbyboardid = this.base_url+"/ListByBoardId";
  createcard = this.base_url+"/List-card";
  cardbylistid = this.base_url+"/CardByListId";
  updateteam = this.base_url+"/updateTeam";
  updateuser = this.base_url+"/updateUser";


  createBoardd(board:Board){
   
    return this.http.post(this.postboard +'/',board);

  }

  createTmBoardd(tboard:Teamboard){
   
    return this.http.post(this.postboard +'/',tboard);

  }

   getBoardById(user_id:number){
     return this.http.get(this.getboardByUserId+'/'+user_id);
   }

   getBoardId(_id:number){
    return this.http.get(this.getboardbyid+'/'+_id);
  }
  getBoardTeam(_id:number){
    return this.http.get(this.getboardbyteam+'/'+_id);
  }

  getStarBoard(user_id:number){

    return this.http.get(this.getstarboard+'/'+user_id);

  }

   addstarBoard(board:Board,_id:number){
     return this.http.post(this.addstarboard+'/'+_id,board);
   }

   removestarBoard(board:Board,_id:number){
    return this.http.post(this.removestarboard+'/'+_id,board);
  }

  createListt(list:List){
    return this.http.post(this.createlist+"/",list);
  }

  createCardd(card:Card){
    return this.http.post(this.createcard+"/",card);
  }

  getListbyboard(_id:string){
    return this.http.get(this.listbyboardid+"/"+_id);
  }

  getCardbylist(list_id:string){
    return this.http.get(this.cardbylistid+"/"+list_id);
  }
  
  getTeamStar(_id:string){

  return this.http.get(this.getteamstarboard+"/"+_id);

  }
  
  updateTeam(_id:number,team:Teams){
    
      return this.http.put(this.updateteam+"/"+_id,team);

  }

  updateUser(_id:number,auth:Auth){
    
    return this.http.put(this.updateteam+"/"+_id,auth);

}

}
