import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { Auth } from './auth';
import { BehaviorSubject } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

checkStatus = new BehaviorSubject<boolean>(false);

isUserLoggedIn = this.checkStatus.asObservable();


 constructor(private http:HttpClient) { }
   checkLogin(){

    const token = localStorage.getItem('email');

    if(token)
    {

      this.checkStatus.next(true);

    }
    
    else{
      this.checkStatus.next(false);
    }
   }

loginapi ="http://localhost:3000/api/login";

loginUser(auth:Auth){

  return this.http.post(this.loginapi,auth).subscribe((checkuser:any)=>{

    if(checkuser.email){

      localStorage.setItem('_id',JSON.stringify(checkuser._id));      
      localStorage.setItem('email',JSON.stringify(checkuser.email));
      localStorage.setItem('password',JSON.stringify(checkuser.password));
      localStorage.setItem('uname',JSON.stringify(checkuser.uname));
      localStorage.setItem('date',JSON.stringify(checkuser.date));

      this.checkLogin();
    }

  });

}


regapi = "http://localhost:3000/api/user";


   getuser(id:number){

       return this.http.get(this.regapi+''+id);

   }
   

    userReg(auth:Auth):Observable<Auth>{

     return this.http.post<Auth>(this.regapi,auth);

    }

}
