import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HomeComponent } from './homepage/home/home.component';
import { BoardComponent } from './board/board/board.component';
import { UserprofileComponent } from './profile/userprofile/userprofile.component';
import { PagenotfoundComponent } from './pagenotfound/pagenotfound.component';
import { DetailComponent } from './homepage/detail/detail.component';
import { TeamprofileComponent } from './teamprofile/teamprofile/teamprofile.component';
import { SignupComponent } from './auth/signup/signup.component';
import { LoginComponent } from './auth/login/login.component';
import { GuestGuard } from './Guards/guest.guard';
import { AuthGuard } from './Guards/auth.guard';


const routes: Routes = [
 
  {path:'',redirectTo:'signup',pathMatch:'full'},
  // {path:'home/board/:id/:title/:color',component:BoardComponent},
  {path:'detail',component:DetailComponent},
  {path:'home/:user_id',component:HomeComponent,canActivate:[AuthGuard]},
  {path:'board/:_id',component:BoardComponent},
  {path:'teamprofile/:_id',component:TeamprofileComponent},
  {path:'userprofile',component:UserprofileComponent},
  {path:'signup',component:SignupComponent,canActivate:[GuestGuard]},
  {path:'login',component:LoginComponent,canActivate:[GuestGuard]},
  {path:'**',component:PagenotfoundComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes,{ useHash: false })],
  exports: [RouterModule]
})
export class AppRoutingModule { }


